  storeName.value = storeModule.GET_ALL.value.name;
};

// Watchers
watch(() => storeModule.GET_STORE_HOURS.value, initStoreHours);
watch(() => storeModule.GET_ALL.value.name, initStoreName);

onMounted(() => {
  initStoreHours();
  initStoreName();
});

